<template>
  <VueLoading :active="isLoading" />
  <html>
    <body>
      <div class="container login">
        <div class="row justify-content-center px-3 px-sm-0">
          <div class="col-12 col-md-6">
            <form class="row g-3 p-4 bg-warning" @submit.prevent="login">
              <h1 class="mb-3 text-center text-danger fw-bold">請先登入</h1>
              <div class="col-12">
                <label
                  for="username"
                  class="form-label fw-bold text-danger fs-4"
                  >帳號</label
                >
                <input
                  type="text"
                  class="form-control"
                  id="username"
                  placeholder="name@example.com"
                  v-model="user.username"
                  required
                  autofocus
                />
              </div>
              <div class="col-12">
                <label
                  for="password"
                  class="form-label fw-bold text-danger fs-4"
                  >密碼</label
                >
                <input
                  type="password"
                  class="form-control"
                  id="password"
                  v-model="user.password"
                  required
                />
              </div>

              <div class="col-12 col-md-6 text-nowrap">
                <button type="submit" class="w-100 btn btn-primary">
                  登入
                </button>
              </div>
              <div class="col-12 col-md-6 text-nowrap">
                <RouterLink to="/" class="w-100 btn btn-primary"
                  >回首頁</RouterLink
                >
              </div>
            </form>
          </div>
        </div>
      </div>
    </body>
  </html>
</template>

<script>
export default {
  data() {
    return {
      user: {
        username: "",
        password: "",
      },
      isLoading: false,
    };
  },

  methods: {
    login() {
      const url = `${process.env.VUE_APP_API}/admin/signin`;
      this.$http
        .post(url, this.user)
        .then((res) => {
          const { token, expired } = res.data;
          // 回傳的 token 存到 hexToken
          document.cookie = `myToken=${token}; expires=${new Date(expired)};`;
          this.$router.push("/admin/products");
        })
        .catch((err) => {
          alert(err.response.data.message);
          this.user.password = "";
        });
    },
    loadingEffect() {
      this.isLoading = true;
      setTimeout(() => {
        this.isLoading = false;
      }, 500);
    },
  },
  mounted() {
    this.loadingEffect();
  },
};
</script>

<style lang="scss" scoped>
html,
body {
  height: 100vh;
}

body {
  background-image: url(../assets/images/login-bg.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.login {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login > form {
  background-color: rgba(0, 0, 0, 0.1);
}
</style>
